import * as React from 'react';
import { useState } from 'react';
import { BorderedBox, DatesWrapper } from './styles';
import { Day } from './Day';
import { IDate } from '../../types/Days';
import { useTranslation } from 'react-i18next';
function a11yProps(index: number) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}
const Days: React.FC<any> = () => {
    const { i18n } = useTranslation();
    const dates: IDate[] = [
        { id: 0, monthAr: '', dayAr: '', month: 'Nov', day: 'Mon', date: '1', locked: true },
        { id: 1, monthAr: '', dayAr: '', month: 'Dec', day: 'Tue', date: '2' },
        { id: 2, monthAr: '', dayAr: '', month: 'Jan', day: 'Wed', date: '3' },
        { id: 3, monthAr: '', dayAr: '', month: 'Feb', day: 'Thr', date: '4' },
        { id: 4, monthAr: '', dayAr: '', month: 'Mar', day: 'Fri', date: '5', off: 'off', offAr: '' },
        { id: 5, monthAr: '', dayAr: '', month: 'Apr', day: 'Sat', date: '6' },
        { id: 6, monthAr: '', dayAr: '', month: 'May', day: 'Sun', date: '7' },
        { id: 3, monthAr: '', dayAr: '', month: 'Jun', day: 'Thr', date: '4' },
        { id: 4, monthAr: '', dayAr: '', month: 'July', day: 'Fri', date: '5', off: 'off' },
        { id: 5, monthAr: '', dayAr: '', month: 'Aug', day: 'Sat', date: '6' },
        { id: 6, monthAr: '', dayAr: '', month: 'Sep', day: 'Sun', date: '7' },
        { id: 6, monthAr: '', dayAr: '', month: 'Oct', day: 'Sun', date: '7' },
    ];
    const [active, setActive] = useState<number>(2);
    const handleChange = (event: any, newValue: any) => {
        if (newValue !== 4) {
            setActive(newValue);
        }
    };
    const handleClick = (item: IDate) => {
        if (!item.off) {
            setActive(item.id);
        }
    };

    return (
        <BorderedBox>
            <DatesWrapper
                value={active}
                onChange={handleChange}
                variant="scrollable"
                scrollButtons="auto"
                allowScrollButtonsMobile
                aria-label="scrollable auto tabs days"
                dir={i18n.dir()}
            >
                {dates.map((item, index) => {
                    const cls = `${
                        item.off ? 'disabled' : item.locked ? 'locked' : active === item.id ? 'active' : ''
                    }`;
                    return (
                        <Day
                            {...a11yProps(index)}
                            item={item}
                            className={cls}
                            key={`day=${item.id}`}
                            onClick={() => handleClick(item)}
                        />
                    );
                })}
            </DatesWrapper>
        </BorderedBox>
    );
};

export default Days;
